.main-pic {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

h1 {
  text-align: center;
}

.flex {
  display: flex;
  width: 80%;
  margin: auto;
  flex-wrap: wrap;
  justify-content: center;
}

.horizontal {
  display: flex;
  justify-content: center;
  width: 80%;
  margin: auto;
  list-style: none;
  font-size: 22px;
  gap: 20px;
  flex-wrap: wrap;
}
.vertical {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 25px 0;
}

.horizontal li {
  cursor: pointer;
}

.horizontal li:hover {
  text-decoration: underline;
  color: #ffeb3b;
}
.buttons-sort {
  display: flex;
  justify-content: center;
  gap: 10px;
}
.product-item img {
  width: 200px;
  border-radius: 50%;
  height: 200px;
  object-fit: cover;
}

.product-item {
  width: 200px;
  text-align: center;
  margin: 30px 10px;
}

.product-item button,
.vertical button {
  padding: 9px 33px;
  border: none;
  outline: none;
  background: #ffeb3b;
  text-transform: uppercase;
  font-weight: bold;
}

.product-item button:hover {
  background: gray;
  color: #fff;
  cursor: pointer;
}
.card-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}
.total {
  font-size: 20px;
  font-weight: bolder;
  font-family: tahoma;
}
.card {
  display: flex;
  width: 80%;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}
.card-item {
  max-width: 200px;
  background-color: #03a9f47d;
  padding: 10px;
  margin: 10px;
}
.card-item.more-than-twenty {
  background-color: #ffeb3b;
}
.card-item .title {
  font-weight: bold;
}
.title {
  margin: 0;
}
.card-item .subtotal {
  font-style: italic;
  font-weight: bold;
}

.del-btn {
  color: #fff;
  background: orangered;
  border: none;
  padding: 4px;
  margin-left: 10px;
  font-weight: bold;
}
@media (max-width: 457px) {
  .buttons-sort {
    flex-direction: column;
  }
}
